<!-- 計算目標預算與配比 -->
<template>
  <div class="target">
    <!-- 計算目標 -->
    <div class="target-title">計算目標</div>
    <div class="target-type">
      <el-radio
        class="option-item radio"
        v-model="target.type"
        label="budget">
        輸入目標預算
      </el-radio>
      <el-radio
        class="option-item radio"
        v-model="target.type"
        label="reach">
        輸入目標Reach
      </el-radio>
    </div>
    <div v-show="target.type === 'budget'" class="target-value">
      <span>預算</span>
      <el-input type="text" v-model="budgetFormat"></el-input>
    </div>
    <div v-show="target.type === 'reach'" class="target-value">
      <span>Reach</span>
      <el-input type="number" v-model.number="target.reach.value" :min="0" :max="100" :step="1" @change="reachFormat"></el-input>
      <span>%</span>
    </div>
    <!-- 警示文字 -->
    <span
      class="target-budget-warning"
      v-if="warningText.value">
      {{ warningText.value }}
    </span>
    <!-- 廣告格式 -->
    <div class="target-title">
      {{ target.type === 'budget' ? '廣告格式&預算配比' : '廣告格式' }}
      <el-popover placement="top-end" trigger="click">
        <div class="target-format-info">
          <div class="target-format-info-item">
            <img :src="require(`@/assets/images/ad_format_mir.png`)" width="160px">
            <span>MIR</span>
          </div>
          <div class="target-format-info-item" v-if="!isPrime">
            <img :src="require(`@/assets/images/ad_format_ip.png`)" width="160px">
            <span>IP</span>
          </div>
          <div class="target-format-info-item">
            <img :src="require(`@/assets/images/ad_format_ir.png`)" width="160px">
            <span>IR</span>
          </div>
        </div>
        <el-button type="text" slot="reference">( 格式示意圖 )</el-button>
      </el-popover>
    </div>
    <!-- Budget 模式 -->
    <div v-show="target.type === 'budget'" class="target-budget">
      <div class="target-budget-input">
        <span>Mobile In-Read</span>
        <el-input-number
          v-model.number="target.budget.format.MIR"
          @blur="checkBudgetFormatValue('MIR')"
          :step="5"
          :precision="0"
          :min="0"
          :max="checkMaxRatio('MIR')"
          controls-position="right"
          size="mini">
        </el-input-number>
      </div>
      <div class="target-budget-input" v-if="!isPrime">
        <span>In-Page</span>
        <el-input-number
          v-model.number="target.budget.format.IP"
          @blur="checkBudgetFormatValue('IP')"
          :step="5"
          :precision="0"
          :min="0"
          :max="checkMaxRatio('IP')"
          controls-position="right"
          size="mini">
        </el-input-number>
      </div>
      <div class="target-budget-input">
        <span>In-Read</span>
        <el-input-number
          v-model.number="target.budget.format.IR"
          @blur="checkBudgetFormatValue('IR')"
          :step="5"
          :precision="0"
          :min="0"
          :max="checkMaxRatio('IR')"
          controls-position="right"
          size="mini">
        </el-input-number>
      </div>
      <!-- 警示文字 -->
      <span
        class="target-budget-warning"
        v-if="warningText.ratio">
        {{ warningText.ratio }}
      </span>
    </div>
    <!-- Reach 模式 -->
    <div v-show="target.type === 'reach'" class="target-reach">
      <el-radio
        class="option-item radio"
        :disabled="true"
        checked>
        Mobile In-Read
      </el-radio>
      <el-radio
        class="option-item radio"
        v-if="!isPrime"
        v-model="target.reach.format"
        label="IP">
        In-Page
      </el-radio>
      <el-radio
        class="option-item radio"
        v-model="target.reach.format"
        label="IR">
        In-Read
      </el-radio>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
export default {
  name: 'BudgetReach',
  data () {
    const defaultData = () => {
      return {
        type: 'budget',
        budget: {
          value: 100000,
          format: {
            MIR: 100,
            IR: 0,
            IP: 0
          }
        },
        reach: {
          value: 0,
          format: 'IR' // MIR必定存在，不需額外送
        }
      }
    }
    return {
      defaultData,
      target: defaultData()
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource', 'selectedData']),
    isPrime () {
      return this.selectedData.media.isPrime
    },
    // 預算欄位的千分位formatter
    budgetFormat: {
      set (val) {
        const budget = val.replace(/\$|,|[^\d]/g, '')
        this.target.budget.value = !budget || isNaN(budget) ? 0 : parseInt(budget, 10)
      },
      get () {
        return isNaN(this.target.budget.value)
          ? 0
          : this.target.budget.value.toLocaleString('en-US', {
            style: 'currency',
            currency: 'USD',
            minimumFractionDigits: 0
          })
      }
    },
    // 剩下的格式配比百分比
    restRatio () {
      return 100 - this.target.budget.format.MIR - this.target.budget.format.IP - this.target.budget.format.IR
    },
    warningText () {
      const warning = {}
      if (this.target.type === 'budget' && this.target.budget.value < 100000) {
        warning.value = '最低計算之目標預算為$100,000，請增加目標預算金額'
      }
      if (this.target.type === 'reach' && (this.target.reach.value < 1 || this.target.reach.value > 100)) {
        warning.value = '目標reach必須介於0~100%'
      }
      if (this.restRatio && this.target.type === 'budget') {
        warning.ratio = `※廣告格式總合必須為100%, 剩餘${this.restRatio}%未使用`
      }
      return warning
    }
  },
  watch: {
    // isPrime 不可選 IP
    isPrime: {
      handler () {
        if (this.isPrime) {
          this.target.budget.format.IP = 0
          this.target.reach.format = 'IR'
        }
      },
      immediate: true
    },
    target: {
      handler () {
        this.updateData()
      },
      deep: true,
      immediate: true
    },
    options: {
      handler () {
        this.target = this.defaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        // 載入計算結果時將預算設成預設值
        this.target = this.defaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 如果預算為空，設為0
    checkBudgetFormatValue (type) {
      this.target.budget.format[type] = this.target.budget.format[type] || 0
    },
    //  移除reach輸入的小數點
    reachFormat (val) {
      this.target.reach.value = Math.floor(val)
    },
    // 檢查可使用配比百分比上限
    checkMaxRatio (type) {
      switch (type) {
        case 'MIR':
          return 100 - this.target.budget.format.IP - this.target.budget.format.IR
        case 'IP':
          return 100 - this.target.budget.format.MIR - this.target.budget.format.IR
        case 'IR':
          return 100 - this.target.budget.format.MIR - this.target.budget.format.IP
      }
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'budgetReach', value: !this.$lodash.isEmpty(this.warningText) })
      this.updateSelectedData([
        { key: 'budgetReach', value: this.target },
        {
          key: 'condition.mode',
          value: {
            request: this.target.type === 'budget' ? 0 : 1,
            quantity: this.target.type === 'budget' ? this.target.budget.value : this.target.reach.value / 100
          }
        },
        {
          key: 'condition.ad_format',
          value: this.target.type === 'budget'
            ? {
              MIR: this.target.budget.format.MIR / 100,
              IP: this.target.budget.format.IP / 100,
              IR: this.target.budget.format.IR / 100
            }
            : {
              MIR: true,
              IP: this.target.reach.format === 'IP',
              IR: this.target.reach.format === 'IR'
            }
        }
      ])
    }
  }
}
</script>

