<!-- 地區選擇 -->
<template>
  <div class="city">
    <!-- 四區 -->
    <div class="option">
      <span class="option-title required">地區選擇</span>
      <span
        class="option-warning"
        v-if="warningText">
        {{ warningText }}
      </span>
      <div class="option-content--column regions">
        <el-checkbox
          @change="toggleRegionAll"
          :value="isCheckedAllRegion">
          四區：全選
        </el-checkbox>
        <el-checkbox-group
          class="option-item cities"
          v-model="cities.regions">
          <el-checkbox
            v-for="(region, index) in options.regions.filter(region => region.id !== offshoreIslandIds)"
            :label="region.id"
            :key="index">
            {{ region.name }}
          </el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
    <!-- 六都 -->
    <div class="option">
      <div class="option-content--column municipalities">
        <el-checkbox
          @change="toggleMunicipalityAll"
          :value="isCheckedAllMunicipality">
          六都：全選
        </el-checkbox>
        <el-checkbox-group
          class="option-item cities"
          v-model="cities.municipalities">
          <el-checkbox
            v-for="(municipality, index) in options.municipalities"
            :label="municipality.id"
            :key="index">
            {{ municipality.city_tw }}
          </el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'City',
  props: {
    options: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      offshoreIslandIds: 5, // 離島地區groupId, compare/index 也有一個
      cities: {
        regions: [],
        municipalities: []
      }
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    // 提示文字
    warningText () {
      return this.selectedCityIds.length < 4 && this.$lodash.isEmpty(this.cities.regions)
        ? '※請至少選擇1個區域或4個都市'
        : null
    },
    // 四區Ids, 不包含離島Ids
    allRegionIds () {
      return this.options.regions
        .filter(region => region.id !== this.offshoreIslandIds)
        .map(region => region.id)
    },
    // 六都Ids
    allMunicipalityIds () {
      return this.options.municipalities.map(municipality => municipality.id)
    },
    // 是否已全選四區
    isCheckedAllRegion () {
      return this.allRegionIds.every(region => this.cities.regions.includes(region))
    },
    // 是否已全選六都
    isCheckedAllMunicipality () {
      return this.cities.municipalities.length === this.options.municipalities.length
    },
    // 攤平所有已選的city_id
    selectedCityIds () {
      const cityIds = []
      this.cities.regions.forEach((id, index) => {
        this.options.regions.find(region => {
          if (region.id === id) {
            cityIds.push(...region.city_ids)
          }
        })
      })
      cityIds.push(...this.cities.municipalities)
      return [...new Set(cityIds)]
    }
  },
  watch: {
    isCheckedAllRegion () {
      if (this.isCheckedAllRegion) {
        this.cities.regions = [...this.allRegionIds, this.offshoreIslandIds]
      } else {
        this.cities.regions.splice(this.cities.regions.indexOf(this.offshoreIslandIds), 1)
      }
    },
    cities: {
      handler () {
        if (!this.$lodash.isEmpty(this.options.regions)) {
          // @TODO 更名
          this.updateData()
        }
      },
      deep: true,
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 預設值設定
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.cities)
      this.cities.regions = dataSource.regions || this.allRegionIds
      this.cities.municipalities = dataSource.municipalities || this.allMunicipalityIds
    },
    // 四區全選切換
    toggleRegionAll (isChecked) {
      this.cities.regions = isChecked
        ? [...this.allRegionIds, this.offshoreIslandIds]
        : []
    },
    // 六都全選切換
    toggleMunicipalityAll (isChecked) {
      this.cities.municipalities = isChecked ? this.allMunicipalityIds : []
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'cities', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'cities', value: this.cities },
        {
          key: 'condition.cities',
          value: {
            all_selected: this.isCheckedAllRegion,
            city_ids: this.selectedCityIds
          }
        }
      ])
    }
  }
}
</script>

<style lang="scss" scoped>
.city {
  .el-checkbox-group {
    display: flex;
    flex-wrap: wrap;
    .el-checkbox {
      margin-left: 0;
      margin-right: 48px;
      width: 80px;
    }
  }
}
</style>
