<!-- 時段選擇 -->
<template>
  <div class="hourlies">
    <div class="option">
      <span class="option-title required">時段選擇</span>
      <span
        class="option-warning"
        v-if="warningText">
        {{ warningText }}
      </span>
      <div class="option-content--column">
        <el-checkbox
          @change="toggleAll"
          :value="isSelectedAll">
          全選
        </el-checkbox>
        <el-checkbox-group
          class="option-item"
          v-model="hourlies">
          <el-checkbox
            v-for="(item, index) in options"
            :label="item.id"
            :key="index">
            {{ item.name }}
          </el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Hourlies',
  props: {
    options: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      hourlies: []
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    // 提示文字
    warningText () {
      return this.hourlies.length === 0 ? '※請至少選擇一個時段' : null
    },
    // 全部時段id
    allId () {
      return this.options.map(option => option.id)
    },
    // 是否已全選
    isSelectedAll () {
      return this.allId.every(id => this.hourlies.includes(id))
    }
  },
  watch: {
    hourlies: {
      handler () {
        this.updateData()
      },
      deep: true,
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.hourlies)
      this.hourlies = dataSource || this.allId
    },
    // 全選切換
    toggleAll (isChecked) {
      this.hourlies = isChecked ? this.allId : []
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'hourlies', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'hourlies', value: this.hourlies },
        { key: 'condition.hour_ids', value: this.hourlies }
      ])
    }
  }
}
</script>
