<!-- 計算頁面 -->
<template>
  <div id="Calculation" v-loading="optionLoading">
    <!-- 頂部 -->
    <div class="header">
      <!-- 基本資料 -->
      <BaseInfo/>
    </div>
    <!-- 內容區 -->
    <div class="container">
      <div class="container-left" @scroll="scrollEvent">
        <!-- 條件組合工具列 -->
        <FavoriteTool :scrollTop="leftScrollTop" :favoriteData="dataSource"/>
        <!-- 廣告走期 -->
        <PlayDate :dataSource="dataSource"/>
        <!-- 素材秒數 -->
        <FlvSecond :options="options.flv_second" :dataSource="dataSource.flvSecond"/>
        <!-- 頻次上限 -->
        <Frequency :dataSource="dataSource.frequency"/>
        <!-- 年齡性別 -->
        <Demographic :options="options.demographics" :dataSource="dataSource"/>
        <!-- 時段選擇 -->
        <Hourlies :options="options.hourlies" :dataSource="dataSource.hourlies"/>
        <!-- 興趣與特定議題 -->
        <InterestKeyword :options="InterestKeywordData" :dataSource="dataSource.interestKeyword"/>
        <!-- 媒體類型 -->
        <Media :options="options.medias.categories" :dataSource="dataSource.media"/>
        <!-- 地區選擇 -->
        <Cities :options="options.cities" :dataSource="dataSource.cities"/>
        <!-- 電信商 -->
        <Isp :options="options.isps" :dataSource="dataSource.isps"/>
        <!-- 手機品牌 -->
        <DeviceBrand :options="options.device_brands" :dataSource="dataSource.deviceBrands"/>
        <!-- 底部提示 -->
        <div class="line">
          <div class="line-text">填選完成，請至右側輸入計算目標</div>
        </div>
      </div>
      <div class="container-right">
        <!-- 計算目標預算與配比 -->
        <BudgetReach/>
        <!-- 送出計算 -->
        <el-button
          class="btn-counter"
          v-loading="dataLoading"
          @click="createQuickPlanInfo"
          :disabled="!requireAllPass || reCalculationLock">
          開始計算
        </el-button>
        <!-- 計算結果/加入比較 -->
        <Result :calculationResult="calculationResult"/>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions, mapGetters } from 'vuex'
import conditionApi from '@/api/conditions'
//
import FavoriteTool from './favorite/FavoriteTool'
//
import BaseInfo from './options/BaseInfo'
import PlayDate from './options/PlayDate'
import FlvSecond from './options/FlvSecond'
import Demographic from './options/Demographic'
import InterestKeyword from './options/InterestKeyword'
import Media from './options/Media'
import Hourlies from './options/Hourlies'
import Frequency from './options/Frequency'
import Cities from './options/Cities'
import Isp from './options/Isp'
import DeviceBrand from './options/DeviceBrand'
import BudgetReach from './options/BudgetReach'
//
import Result from './result/Result'

export default {
  name: 'Calculation',
  components: {
    FavoriteTool,
    BaseInfo,
    PlayDate,
    FlvSecond,
    Demographic,
    InterestKeyword,
    Media,
    Hourlies,
    Frequency,
    Cities,
    Isp,
    DeviceBrand,
    BudgetReach,
    Result
  },
  data () {
    return {
      optionLoading: true,
      leftScrollTop: 0,
      calculationResult: {},
      reCalculationLock: false
    }
  },
  computed: {
    ...mapState('root', ['dataLoading']),
    ...mapState('calculation', ['options', 'dataSource', 'selectedData']),
    ...mapGetters('root', ['userId']),
    ...mapGetters('calculation', ['requireAllPass']),
    InterestKeywordData () {
      return {
        interests: this.options.interests,
        keywords: this.options.keywords
      }
    }
  },
  watch: {
    selectedData: {
      handler () {
        this.reCalculationLock = false
      },
      deep: true
    },
    dataSource: {
      handler () {
        // 載入計算結果時將結果清空
        this.calculationResult = {}
      },
      deep: true
    }
  },
  created () {
    this.getOptionListData()
  },
  methods: {
    ...mapActions('root', ['setDataLoading']),
    ...mapActions('calculation', ['setOptions']),
    // 取得選項資料
    getOptionListData () {
      return conditionApi.getOptionListData().then(res => {
        this.setOptions(res.data)
        this.optionLoading = false
      })
    },
    // 開始運算(先建立資料後送運算)
    createQuickPlanInfo () {
      this.setDataLoading(true)
      this.$message.closeAll()
      const conditions = this.$lodash.cloneDeep(this.selectedData)
      conditions.current_user_id = this.userId
      this.calculationResult = {}
      return conditionApi.createQuickPlanInfo(conditions)
        .then(res => {
          const id = res.data.data.qp_condition_id
          conditionApi.doCalculation(id).then(res => {
            this.calculationResult = res.data.data
            this.reCalculationLock = true
          })
        })
        .catch(err => {
          this.$message.error(err.response.data.message)
        })
    },
    scrollEvent (e) {
      this.leftScrollTop = e.target.scrollTop
    }
  }
}
</script>

