
<!-- 計算結果/加入比較 -->
<template>
  <div id="Result">
    <!-- 計算結果 -->
    <div class="result" v-if="!$lodash.isEmpty(calculationResult)">
      <div class="result-table">
        <div class="result-title">
          計算結果
        </div>
        <!-- 警示模式：不顯示量體，只顯示警示文字 -->
        <div
          class="result-warning"
          v-if="calculationResult.is_warning && !calculationResult.sim_result_display">
          {{ calculationResult.warning_message }}
        </div>
        <!-- 正常狀態 -->
        <div v-else>
          <div class="result-table-row head">
            <div class="result-table-column text-right"></div>
            <div class="result-table-column">Mobile</div>
            <div class="result-table-column">Desktop</div>
          </div>
          <div class="result-table-row">
            <div class="result-table-column text-right">符合條件UU數</div>
            <div class="result-table-column">{{ toLocaleString(calculationResult.mobile.total_uu) }}</div>
            <div class="result-table-column">{{ toLocaleString(calculationResult.desktop.total_uu) }}</div>
          </div>
          <div class="result-table-row">
            <div class="result-table-column text-right">預算</div>
            <div class="result-table-column">${{ toLocaleString(calculationResult.mobile.budget) }}</div>
            <div class="result-table-column">${{ toLocaleString(calculationResult.desktop.budget) }}</div>
          </div>
          <div class="result-table-row">
            <div class="result-table-column text-right">可觸及UU數</div>
            <div class="result-table-column">{{ toLocaleString(calculationResult.mobile.reach_uu) }}</div>
            <div class="result-table-column">{{ toLocaleString(calculationResult.desktop.reach_uu) }}</div>
          </div>
          <div class="result-table-row">
            <div class="result-table-column text-right">觸及佔比(Reach%)</div>
            <div class="result-table-column">{{ toPercentage(calculationResult.mobile.reach) }}</div>
            <div class="result-table-column">{{ toPercentage(calculationResult.desktop.reach) }}</div>
          </div>
        </div>
        <!-- 功能按鈕 -->
        <el-input
          class="result-table-name"
          v-model="resultName"
          :placeholder="resultName || `計算結果${compareDataList.length + 1}`">
        </el-input>
        <div class="result-table-buttons">
          <!-- @TODO: 匯出 -->
          <!-- <el-button class="btn-export">匯出</el-button> -->
          <!-- 加入比較 -->
          <el-button
            class="btn-addCompare"
            @click="addToCompareList"
            type="primary">
            {{ isAdded ? '已加入比較清單' : '加入結果比較' }}
          </el-button>
        </div>
      </div>
    </div>
    <!-- 計算結果線圖 -->
    <div
      class="result-chart"
      v-if="!$lodash.isEmpty(calculationResult) && calculationResult.sim_result_display !== false">
      <el-radio-group v-model="resultChartData.type">
        <el-radio-button :disabled="!resultChartData.mobile" label="mobile"></el-radio-button>
        <el-radio-button :disabled="!resultChartData.desktop" label="desktop"></el-radio-button>
      </el-radio-group>
      <HighChart :chartOptions="resultChartConfig" :resultChartData="resultChartData"></HighChart>
    </div>
    <!-- 計算結果餅圖 -->
    <!-- @TODO 餅圖 -->
    <!-- <div class="result-pie"></div> -->
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import resultChartConfig from '@/config/charts/resultChartConfig'
import HighChart from '@/components/HighChart'

export default {
  name: 'Result',
  components: {
    HighChart
  },
  props: {
    calculationResult: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      resultChartConfig,
      resultName: '',
      chartOptions: {},
      resultChartData: {
        type: 'mobile',
        desktop: [],
        mobile: []
      },
      isAdded: false
    }
  },
  computed: {
    ...mapState('root', ['dataLoading']),
    ...mapState('calculation', ['selectedData', 'compareDataList']),
    ...mapGetters('root', ['userId']),
    ...mapGetters('calculation', ['isCompareDataLimit'])
  },
  watch: {
    calculationResult: {
      handler () {
        // 如果傳入資料是空的，
        if (this.$lodash.isEmpty(this.calculationResult)) {
          this.resultName = ''
          this.resultChartData = {
            type: '',
            desktop: [],
            mobile: []
          }
        } else {
          if (this.calculationResult.is_warning && this.calculationResult.sim_result_display) {
            this.$message.error({
              duration: 0,
              showClose: true,
              message: this.calculationResult.warning_message
            })
          }
          this.isAdded = false
          this.setChart()
          // 滾動效果
          const target = document.querySelector('.btn-counter')
          const rightWindowScrollTop = document.querySelector('.container-right').scrollTop
          this.$nextTick(() => {
            this.scrollAnimation(rightWindowScrollTop, target.offsetTop - 8)
          })
        }
      },
      deep: true
    }
  },
  methods: {
    ...mapActions('calculation', ['addCompareData']),
    // 加入比較
    addToCompareList () {
      if (this.isCompareDataLimit) {
        this.$alert('計算組合比較最多十筆。')
      } else {
        this.resultName = this.resultName || `計算結果${this.compareDataList.length + 1}`
        this.addCompareData({
          resultName: this.resultName,
          selectedData: this.$lodash.cloneDeep(this.selectedData),
          calculationResult: this.$lodash.cloneDeep(this.calculationResult)
        })
        this.isAdded = true
        this.$confirm(`已加入結果比較，紀錄為 - ${this.resultName}`, {
          cancelButtonText: '留在本頁',
          confirmButtonText: '前往結果比較'
        }).then(() => {
          this.$router.push({ name: 'Compare' })
        }).catch(() => {})
      }
    },
    // 轉數字
    toLocaleString (number) {
      return number ? number.toLocaleString() : 0
    },
    // 轉百分比
    toPercentage (number) {
      return number ? `${Math.round(number * 100)}%` : `0%`
    },
    // 圖表設定
    setChart () {
      // 找到desktop的mark點，將data內相同的點替換掉給圖表顯示大點用
      const desktopMark = this.calculationResult.desktop.graphic.marker
      const desktopData = desktopMark
        ? this.calculationResult.desktop.graphic.data.map(item => {
          if (item[0] === desktopMark[0] && item[1] === desktopMark[1]) {
            item = {
              x: desktopMark[0],
              y: desktopMark[1],
              marker: { symbol: 'circle', radius: 8 }
            }
          }
          return item
        })
        : null

      // 找到mobile的mark點，將data內相同的點替換掉給圖表顯示大點用
      const mobileMark = this.calculationResult.mobile.graphic.marker
      const mobileData = mobileMark
        ? this.calculationResult.mobile.graphic.data.map(item => {
          if (item[0] === mobileMark[0] && item[1] === mobileMark[1]) {
            item = {
              x: mobileMark[0],
              y: mobileMark[1],
              marker: { symbol: 'circle', radius: 8 }
            }
          }
          return item
        })
        : null

      // 設定圖表資料
      this.$nextTick(() => {
        this.resultChartData = {
          type: mobileMark ? 'mobile' : 'desktop',
          desktop: desktopData,
          mobile: mobileData
        }
      })
    },
    scrollAnimation (currentY, targetY) {
      const rightWindow = document.querySelector('.container-right')
      let needScrollTop = targetY - currentY
      let _currentY = currentY
      setTimeout(() => {
        const dist = Math.ceil(needScrollTop / 10)
        _currentY += dist
        rightWindow.scrollTop = _currentY
        if (needScrollTop > 10 || needScrollTop < -10) {
          this.scrollAnimation(_currentY, targetY)
        } else {
          rightWindow.scrollTop = targetY
        }
      }, 10)
    }
  }
}
</script>

