<!-- 電信商 -->
<template>
  <div class="isp" v-if="userType === 'User'">
    <div class="option">
      <span class="option-title required">電信商選擇</span>
      <span
        class="option-warning"
        v-if="warningText">
        {{ warningText }}
      </span>
      <div class="option-content--column">
        <el-checkbox
          @change="toggleAll"
          :value="isSelectedAll">
          全選
        </el-checkbox>
        <el-checkbox-group
          class="option-item"
          v-model="isps">
          <el-checkbox
            v-for="(option, index) in options"
            :label="option.id"
            :key="index">
            {{ option.name }}
          </el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
  name: 'Isp',
  props: {
    options: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      isps: []
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    ...mapGetters('root', ['userType']),
    // 提示文字
    warningText () {
      return this.isps.length === 0 ? '※請至少選擇一家電信商' : null
    },
    // 全部電信商id
    allId () {
      return this.options.map(option => option.id)
    },
    // 是否已全選
    isSelectedAll () {
      return this.allId.every(id => this.isps.includes(id))
    }
  },
  watch: {
    isps: {
      handler () {
        this.updateData()
      },
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.isps)
      this.isps = dataSource || this.allId
    },
    // 全選切換
    toggleAll (isChecked) {
      this.isps = isChecked ? this.allId : []
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'isps', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'isps', value: this.isps },
        {
          key: 'condition.isps',
          value: {
            all_selected: this.isSelectedAll,
            isp_ids: this.isps
          }
        }
      ])
    }
  }
}
</script>

<style lang="scss" scoped>
.isp {
  .el-checkbox-group {
    display: flex;
    flex-wrap: wrap;
    .el-checkbox {
      margin-left: 0;
      margin-right: 16px;
      width: 112px;
    }
  }
}
</style>
