<!-- 素材秒數 -->
<template>
  <div class="flv-second">
    <div class="option">
      <div class="option-title">素材秒數</div>
      <div class="option-content">
        <el-radio
          class="option-item radio"
          v-for="(option, index) in options"
          v-model="flvSecond"
          :label="option.id"
          :key="index">
          {{ option.name }}
        </el-radio>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'FlvSecond',
  props: {
    options: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      flvSecond: 1
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource'])
  },
  watch: {
    flvSecond: {
      handler () {
        this.updateData()
      },
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      this.flvSecond = this.dataSource.flvSecond || 1
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'flvSecond', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'flvSecond', value: this.flvSecond },
        { key: 'condition.flv_second', value: this.flvSecond }
      ])
    }
  }
}
</script>
