<!-- 基本資料 -->
<template>
  <div class="base-info">
    <div class="base-info-content">

      <div class="option">
        <span class="option-title required">客戶名稱</span>
        <span
          class="option-warning"
          v-if="warningText.clientName">
          {{ warningText.clientName }}
        </span>
        <div class="option-content">
          <!-- 外部人員使用input -->
          <el-input
            class="option-item base-input"
            v-if="userType === 'Account'"
            v-model="baseInfo.clientName"
            placeholder="至多20字">
          </el-input>
          <!-- 內部人員使用selector -->
          <el-select
            class="option-item base-input"
            v-if="userType === 'User'"
            v-model="baseInfo.clientId"
            placeholder="請選擇客戶"
            filterable>
            <el-option
              v-for="client in clientList"
              :key="client.client_id"
              :label="client.client_name"
              :value="client.client_id">
            </el-option>
          </el-select>
        </div>
      </div>

      <div class="option">
        <span class="option-title required">廣告活動名稱</span>
        <span
          class="option-warning"
          v-if="warningText.campaignName">
          {{ warningText.campaignName }}
        </span>
        <div class="option-content">
          <el-input
            class="option-item base-input"
            v-model="baseInfo.campaignName"
            placeholder="至多20字">
          </el-input>
        </div>
      </div>

      <div class="option">
        <span class="option-title">代理商名稱</span>
        <div class="option-content">
          <!-- 外部人員使用input -->
          <el-input
            class="option-item base-input"
            v-if="userType === 'Account'"
            v-model="baseInfo.agencyName"
            placeholder="至多20字">
          </el-input>
          <!-- 內部人員使用selector -->
          <el-select
            class="option-item base-input"
            v-if="userType === 'User'"
            v-model="baseInfo.agencyId"
            placeholder="請選擇客戶"
            filterable>
            <el-option
              v-for="agency in agencyList"
              :key="agency.agency_id"
              :label="agency.agency_name"
              :value="agency.agency_id">
            </el-option>
          </el-select>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import conditionApi from '@/api/conditions'
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
  name: 'BaseInfo',
  data () {
    return {
      clientList: [],
      agencyList: [],
      baseInfo: {
        campaignName: '',
        clientId: null,
        clientName: '',
        agencyId: null,
        agencyName: ''
      }
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    ...mapGetters('root', ['userType']),
    warningText () {
      const warning = {}
      if (!this.baseInfo.campaignName) {
        warning.campaignName = '請填寫活動名稱'
      }
      if ((this.userType === 'Account' && !this.baseInfo.clientName) || (this.userType === 'User' && !this.baseInfo.clientId)) {
        warning.clientName = '請填寫客戶名稱'
      }
      return warning
    },
    clientName () {
      return this.userType === 'User' && this.baseInfo.clientId
        ? this.clientList.find(client => client.client_id === this.baseInfo.clientId).client_name
        : this.baseInfo.clientName
    },
    agencyName () {
      return this.userType === 'User' && this.baseInfo.agencyId
        ? this.agencyList.find(agency => agency.agency_id === this.baseInfo.agencyId).agency_name
        : this.baseInfo.agencyName
    }
  },
  watch: {
    baseInfo: {
      handler () {
        this.baseInfo.clientName = this.clientName
        this.baseInfo.agencyName = this.agencyName
        this.updateData()
      },
      deep: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  created () {
    if (this.userType === 'User') {
      this.getClientListData()
      this.getAgencyListData()
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      this.baseInfo.campaignName = this.dataSource.baseInfo.campaignName || ''
      this.baseInfo.clientId = this.dataSource.baseInfo.clientId || null
      this.baseInfo.clientName = this.dataSource.baseInfo.clientName || ''
      this.baseInfo.agencyId = this.dataSource.baseInfo.agencyId || null
      this.baseInfo.agencyName = this.dataSource.baseInfo.agencyName || ''
    },
    getClientListData () {
      conditionApi.getClientListData().then(res => {
        this.clientList = res.data.data
      })
    },
    getAgencyListData () {
      conditionApi.getAgencyListData().then(res => {
        const agencyData = res.data.data
        agencyData.unshift({ agency_id: null, agency_name: '無' })
        this.agencyList = res.data.data
      })
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'baseInfo', value: !this.$lodash.isEmpty(this.warningText) })
      this.updateSelectedData([
        { key: 'baseInfo', value: this.baseInfo },
        { key: 'name', value: this.baseInfo.campaignName },
        { key: 'campaign_name', value: this.baseInfo.campaignName },
        { key: 'client_id', value: this.baseInfo.clientId },
        { key: 'client_name', value: this.clientName },
        { key: 'agency_id', value: this.baseInfo.agencyId },
        { key: 'agency_name', value: this.agencyName }
      ])
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/styles/z-index.scss';
.base-info {
  display: flex;
  flex-direction: column;
  &-title {
    margin-bottom: 8px;
  }
  &-content {
    z-index: map-get($z-index, CalculationHead);
    display: flex;
    background-color: #f8f9fe;
  }
  .option {
    margin-bottom: 16px;
  }
  .option-title {
    margin-bottom: 8px;
  }
}
</style>
