<!-- 興趣與特定議題 -->
<template>
  <div class="option">
    <span class="option-title required">選取興趣/特定議題</span>
    <span
      class="option-warning"
      v-if="warningText">
      {{ warningText }}
    </span>
    <!-- 切換 -->
    <div class="type option-content">
      <el-radio
        class="option-item radio"
        v-model="type"
        label="interest">
        興趣
      </el-radio>
      <el-radio
        class="option-item radio"
        v-model="type"
        label="keyword">
        特定議題
      </el-radio>
      <el-radio
        class="option-item radio"
        v-model="type"
        label="all" :disabled="userType !== 'User'">
        併用 (僅開放OneAD人員使用)
      </el-radio>
    </div>
    <!-- 興趣 -->
    <div
      class="interest-container option-content--column"
      v-show="type === 'interest' || type === 'all'">
      <el-checkbox
        @change="toggleAllInterest"
        :value="isSelectedAllInterests">
        全選
      </el-checkbox>
      <el-checkbox-group
        class="option-item"
        v-model="selectedList.interestIds">
        <!-- @TODO: 檢查後端 -->
        <template v-for="(interest, index) in interestList">
          <el-checkbox
            v-if="userType === 'User' || (userType === 'Account' && interest.id !== nightCatId)"
            :label="interest.id"
            :key="index">
            {{ interest.name }}
          </el-checkbox>
        </template>
      </el-checkbox-group>
    </div>
    <!-- 特定議題-標籤 -->
    <div
      class="option-content--column keyword-container"
      v-if="(type === 'keyword' || type === 'all') && selectedList.keywordIds.length">
      <!-- 標籤 -->
      <template v-for="(interest, index) in interestList">
        <div
          class="tag-container"
          v-if="hasInterestKeyword(interest.id)"
          :key="index">
          <div class="interest">{{interest.name}}</div>
          <div class="keyword">
            <!-- 全選狀態下，顯示已全選文字 -->
            <template v-if="isSelectedAllKeywords(interest.id)">
              <div class="keyword-tag">
                全選
                <i class="el-icon-close" @click="removeAllTag(interest.id)"></i>
              </div>
            </template>
            <!-- 非全選狀態下，僅顯示已選取標籤 -->
            <template v-else>
              <template v-for="(keyword, index) in keywordList.filter(keyword => keyword.interest_id === interest.id)">
                <div
                  class="keyword-tag"
                  v-if="selectedList.keywordIds.includes(keyword.remote_id)"
                  :key="index">
                  {{ keyword.name }}
                  <i class="el-icon-close" @click="removeTag(keyword.remote_id)"></i>
                </div>
              </template>
            </template>
          </div>
        </div>
      </template>
    </div>
    <!-- 特定議題-選取視窗 -->
    <div
      class="popup-menu"
      v-show="type === 'keyword' || type === 'all'">
      <div class="popup-menu-button">
        <el-button
          class="option-item"
          type="primary"
          @click="showPopupMenu = true">
          選取特定議題
        </el-button>
        <span
          class="popup-menu-button--all"
          @click="toggleAllKeywordIds">
          {{selectedList.keywordIds.length === 0 ? '全選' : '清空' }}
        </span>
      </div>
      <!-- 選取介面 -->
      <div class="popup-menu-container">
        <!-- 興趣列表 -->
        <div class="menu" v-show="showPopupMenu">
          <div
            :class="['menu-item', { 'hover': hoverId === interest.id}]"
            v-for="(interest, index) in interestList.filter(interest => interest.id !== nightCatId)"
            :key="index"
            @mouseenter="hoverId = interest.id">
            {{ interest.name }}
            <i class="menu-item-icon el-icon-arrow-right"></i>
          </div>
        </div>
        <!-- 特定議題列表 -->
        <div class="submenu" v-show="showPopupMenu && hoverId">
          <!-- 全選按鈕 -->
          <el-checkbox
            class="menu-item-all"
            :value="hoverKeywordListAllSelected"
            @change="selectAllKeyword">
            全選
          </el-checkbox>
          <el-checkbox-group v-model="selectedList.keywordIds">
            <el-checkbox
              class="menu-item"
              v-for="(keyword, index) in hoverKeywordList"
              :label="keyword.remote_id"
              :key="index">
              {{ keyword.name }}
            </el-checkbox>
          </el-checkbox-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
  name: 'InterestKeyword',
  props: {
    options: {
      type: Object
    }
  },
  data () {
    return {
      type: 'interest',
      showPopupMenu: false,
      selectedList: {
        interestIds: [],
        keywordIds: []
      },
      hoverId: null,
      nightCatId: 16 // 熬夜夜貓族ID, 不顯示於興趣選擇，特定議題僅內部人員可見
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    ...mapGetters('root', ['userId', 'userType']),
    // 提示文字
    warningText () {
      if (this.type.match(/interest|all/) && this.selectedList.interestIds.length === 0) {
        return '※請至少選擇一筆興趣'
      }
      if (this.type.match(/keyword|all/) && this.selectedList.keywordIds.length === 0) {
        return '※請至少選擇一筆特定議題'
      }
      return null
    },
    // 興趣列表
    interestList () {
      return this.options.interests
    },
    // 興趣列表全部ID
    allInterestId () {
      return this.interestList.map(interest => interest.id)
    },
    // 是否已全選興趣
    isSelectedAllInterests (id) {
      return this.allInterestId.every(id => this.selectedList.interestIds.includes(id))
    },
    // 特定議題列表
    keywordList () {
      return this.options.keywords
    },
    // 滑鼠滑動到興趣時顯示其底下的特定議題列表
    hoverKeywordList () {
      return this.hoverId
        ? this.keywordList.filter(keyword => keyword.interest_id === this.hoverId)
        : []
    },
    // 滑鼠滑動到興趣時其底下的特定議題列表全選狀態
    hoverKeywordListAllSelected () {
      return this.hoverKeywordList.every(keyword => this.selectedList.keywordIds.includes(keyword.remote_id))
    },
    // 計算每個interest底下有勾選了幾個keyword後端計算用
    countInterestKeyword () {
      if (!this.$lodash.isEmpty(this.keywordList)) {
        return this.selectedList.keywordIds.reduce((obj, currentId) => {
          const interestId = this.keywordList.find(keyword => keyword.remote_id === currentId).interest_id
          if (!obj[interestId]) {
            obj[interestId] = 0
          }
          obj[interestId]++
          return obj
        }, {})
      }
    }
  },
  watch: {
    type: {
      handler () {
        this.updateData()
      },
      deep: true,
      immediate: true
    },
    selectedList: {
      handler () {
        this.updateData()
      },
      deep: true,
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  created () {
    document.addEventListener('click', this.outsideClick)
  },
  destroyed () {
    document.removeEventListener('click', this.outsideClick)
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.interestKeyword)
      this.type = dataSource.type || 'interest'
      this.selectedList.interestIds = dataSource.interestIds || this.allInterestId
      this.selectedList.keywordIds = dataSource.keywordIds || []
    },
    // === 興趣 ===
    toggleAllInterest (isChecked) {
      this.selectedList.interestIds = isChecked ? this.allInterestId : []
    },
    // ==== 特定議題 ===
    // 全選/全不選
    toggleAllKeywordIds () {
      this.selectedList.keywordIds.length === 0
        ? this.selectedList.keywordIds = this.keywordList.map(keyword => keyword.remote_id)
        : this.selectedList.keywordIds = []
    },
    // 檢查興趣底下的特定議題ID是否存在選取列表中
    hasInterestKeyword (id) {
      return this.keywordList
        .filter(keyword => keyword.interest_id === id)
        .some(keyword => this.selectedList.keywordIds.includes(keyword.remote_id))
    },
    // 是否已全選特定議題
    isSelectedAllKeywords (id) {
      return this.keywordList
        .filter(keyword => keyword.interest_id === id)
        .every(keyword => this.selectedList.keywordIds.includes(keyword.remote_id))
    },
    // 移除tag
    removeTag (keywordId) {
      const keywordIndex = this.selectedList.keywordIds.indexOf(keywordId)
      if (keywordIndex !== -1) {
        this.selectedList.keywordIds.splice(keywordIndex, 1)
      }
    },
    // 移除該興趣底下所有tag
    removeAllTag (id) {
      this.keywordList
        .filter(keyword => keyword.interest_id === id)
        .forEach(keyword => {
          const keywordIndex = this.selectedList.keywordIds.indexOf(keyword.remote_id)
          if (keywordIndex !== -1) {
            this.selectedList.keywordIds.splice(keywordIndex, 1)
          }
        })
    },
    // 選取興趣與底下全部的特定議題
    selectAllKeyword (isChecked) {
      this.hoverKeywordList.forEach(keyword => {
        if (isChecked) {
          this.selectedList.keywordIds.push(keyword.remote_id)
        } else {
          this.selectedList.keywordIds.splice(this.selectedList.keywordIds.indexOf(keyword.remote_id), 1)
        }
      })
      this.selectedList.keywordIds = [...new Set(this.selectedList.keywordIds)]
    },
    // 點擊pop-menu範圍外關閉視窗
    outsideClick (e) {
      const target = e.target
      const el = this.$el.querySelector('.popup-menu')
      if (el !== target && !el.contains(target) && this.showPopupMenu) {
        this.showPopupMenu = !this.showPopupMenu
      }
    },
    // 更新選取資料
    updateData () {
      this.updateSelectedData([
        { key: 'interestKeyword',
          value: {
            type: this.type,
            interestIds: this.selectedList.interestIds,
            keywordIds: this.selectedList.keywordIds
          }
        },
        {
          key: 'condition.interest_ids',
          value: this.type.match(/interest|all/)
            ? this.selectedList.interestIds
            : []
        },
        {
          key: 'condition.keyword_ids',
          value: this.type.match(/keyword|all/)
            ? this.selectedList.keywordIds
            : []
        },
        {
          key: 'condition.target_keywords',
          value: this.type.match(/keyword|all/)
            ? this.countInterestKeyword
            : {}
        }
      ])
      this.updateRequireCheckData({ key: 'interestKeyword', value: Boolean(this.warningText) })
    }
  }
}
</script>

