<!-- 頻次上限 -->
<template>
  <div class="frequency">
    <!-- @TODO: 第一階段先隱藏Freq，但仍送值給後端 -->
    <!-- <div class="option">
      <div class="option-title">頻次上限</div>
      <div class="option-content">
        <el-input-number
          v-model="frequency"
          :min="1"
          :max="10"
          size="mini">
        </el-input-number>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Frequency',
  data () {
    return {
      frequency: 2
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource'])
  },
  watch: {
    frequency: {
      handler () {
        this.updateData()
      },
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      this.frequency = this.dataSource.frequency || 2
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'frequency', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'frequency', value: parseInt(this.frequency, 10) },
        { key: 'condition.freq', value: parseInt(this.frequency, 10) }
      ])
    }
  }
}
</script>

<style lang="scss" scoped>
.frequency {
  .el-input-number {
    width: 100px;
    input {
      padding-right: 12px;
    }
  }
}
