 <!-- 媒體類型 -->
 <template>
  <div class="option">
    <span class="option-title required">媒體類型</span>
    <span
      class="option-warning"
      v-if="warningText">
      {{ warningText }}
    </span>
    <!-- 切換 -->
    <div class="prime-type option-content--column">
      <el-radio
        class="option-item radio"
        v-model="media.isPrime"
        :label="1">
        SPEED PMP PRIME (勾選欲投放的媒體，至少兩家新聞或社群媒體)
      </el-radio>
      <el-radio
        class="option-item radio"
        v-model="media.isPrime"
        :label="0">
        SPEED PMP (勾選欲排除的媒體，最多可剔除10家)
      </el-radio>
    </div>
    <!-- 選擇媒體 -->
    <div class="media-container option-content--column" v-show="showTagContainer">
      <!-- 標籤 -->
      <template v-for="(category, index) in categoryList">
        <div
          class="tag-container"
          v-if="hasCategoryMedia(category.id)"
          :key="index">
          <div class="category">{{category.name}}</div>
          <div class="media">
            <!-- 全選狀態下，顯示已全選文字 -->
            <template v-if="isPrime && isSelectedAllMedia(category.id)">
              <div class="media-tag">
                全選
                <i class="el-icon-close" @click="removeAllTag(category.id)"></i>
              </div>
            </template>
            <!-- 非全選狀態下，僅顯示已選取標籤 -->
            <template v-else>
              <template v-for="(media, index) in mediaList.find(media => media.categoryId === category.id).medias">
                <div
                  :class="['media-tag', { 'exclude': !isPrime }]"
                  v-if="hasMediaId(media.uid)"
                  :key="index">
                  {{ media.description }}
                  <i class="el-icon-close" @click="removeTag(media.uid)"></i>
                </div>
              </template>
            </template>
          </div>
        </div>
      </template>
    </div>
    <!-- 按鈕 -->
    <div class="popup-menu">
      <div class="popup-menu-button">
        <el-button class="option-item" type="primary" @click="showPopupMenu = true">
          選取媒體
        </el-button>
        <span
          class="popup-menu-button--all"
          v-if="isPrime"
          @click="toggleAllMedia">{{media.includeIds.length === 0 ? '全選' : '清空' }}
        </span>
      </div>
      <!-- 選取介面 -->
      <div class="popup-menu-container">
        <!-- 分類列表 -->
        <div class="menu" v-show="showPopupMenu">
          <div
            :class="['menu-item', { 'hover': hoverId === category.id}]"
            v-for="(category, index) in categoryList"
            :key="index"
            @mouseenter="hoverId = category.id">
            {{ category.name }}
            <i class="menu-item-icon el-icon-arrow-right"></i>
          </div>
        </div>
        <!-- 媒體列表 -->
        <div class="submenu" v-show="showPopupMenu && hoverId">
          <!-- 全選按鈕 -->
          <el-checkbox
            class="menu-item-all"
            v-if="isPrime && hoverMediaList.length > 1"
            :value="isHoverMediaAllSelected"
            @change="selectAllHoverMedia">
            全選
          </el-checkbox>
          <!-- include -->
          <el-checkbox-group
            v-if="isPrime"
            v-model="media.includeIds">
            <el-checkbox
              class="menu-item"
              v-for="(media, index) in hoverMediaList"
              :label="media.uid"
              :key="index">
              {{ media.description }}
            </el-checkbox>
          </el-checkbox-group>
          <!-- exclude -->
          <el-checkbox-group
            v-if="!isPrime && media.excludeIds"
            v-model="media.excludeIds"
            :max="maxExcludeMedia">
            <el-checkbox
              class="menu-item"
              v-for="(media, index) in hoverMediaList"
              :label="media.uid"
              :disabled="checkExcludeIds(media.uid)"
              :key="index">
              {{ media.description }}
            </el-checkbox>
          </el-checkbox-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Media',
  props: {
    options: {
      type: Array
    }
  },
  data () {
    return {
      showPopupMenu: false,
      media: {
        isPrime: 1, // 1 = true, 0 = false
        includeIds: [],
        excludeIds: []
      },
      hoverId: null,
      maxExcludeMedia: 10
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    // 警示文字
    warningText () {
      if (this.isPrime) {
        let result = 0
        this.media.includeIds.forEach(id => {
          this.newsAndSocialIds.forEach(data => {
            if (data.includes(id)) {
              result++
            }
          })
        })
        return result < 2 ? '請至少選擇兩家新聞或社群媒體' : null
      }
    },
    // 是否為Prime
    isPrime () {
      return this.media.isPrime === 1
    },
    // 是否顯示tag區域
    showTagContainer () {
      return this.isPrime ? this.media.includeIds.length > 0 : this.media.excludeIds.length > 0
    },
    // 媒體分類列表
    categoryList () {
      return this.options.map(option => {
        return {
          id: option.id,
          name: option.name,
          enable: option.enable
        }
      })
    },
    // 媒體分類列表全部ID
    allCategoriesId () {
      return this.categoryList.map(category => category.id)
    },
    // 媒體列表
    mediaList () {
      return this.options.map(option => {
        return {
          categoryId: option.id,
          medias: option.media
        }
      })
    },
    // 新聞與社交類媒體(檢查用)
    newsAndSocialIds () {
      let newsAndSocialIds = []
      this.mediaList
        .filter(media => media.categoryId === 1 || media.categoryId === 3)
        .forEach(data => newsAndSocialIds.push(...data.medias.map(media => media.uid)))
      return newsAndSocialIds
    },
    // 媒體全部ID
    allMediaId () {
      let allMediaId = []
      this.mediaList.forEach(data => allMediaId.push(...data.medias.map(media => media.uid)))
      return allMediaId
    },
    // 滑鼠滑動到媒體分類時顯示其底下的媒體列表
    hoverMediaList () {
      const mediaList = this.mediaList.find(media => media.categoryId === this.hoverId)
      return mediaList ? mediaList.medias : []
    },
    // 滑鼠滑動到媒體分類時其底下的媒體列表全選狀態
    isHoverMediaAllSelected () {
      return this.hoverMediaList.every(media => this.media.includeIds.includes(media.uid))
    }
  },
  watch: {
    media: {
      handler () {
        this.updateData()
      },
      deep: true,
      immediate: true
    },
    options: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    },
    dataSource: {
      handler () {
        this.setDefaultData()
      },
      deep: true,
      immediate: true
    }
  },
  created () {
    document.addEventListener('click', this.outsideClick)
  },
  destroyed () {
    document.removeEventListener('click', this.outsideClick)
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定預設值
    setDefaultData () {
      const dataSource = this.$lodash.cloneDeep(this.dataSource.media)
      this.media = {
        isPrime: dataSource.isPrime === null ? 1 : dataSource.isPrime,
        includeIds: dataSource.includeIds || [],
        excludeIds: dataSource.excludeIds || []
      }
    },
    // ==== 媒體 ===
    // 全選/全不選
    toggleAllMedia () {
      this.media.includeIds.length === 0
        ? this.media.includeIds = this.$lodash.cloneDeep(this.allMediaId)
        : this.media.includeIds = []
    },
    // 檢查媒體分類底下的媒體ID是否存在選取列表中
    hasCategoryMedia (id) {
      const mediaList = this.mediaList.find(media => media.categoryId === id)
      if (mediaList) {
        return this.isPrime
          ? mediaList.medias.some(media => this.media.includeIds.includes(media.uid))
          : mediaList.medias.some(media => this.media.excludeIds.includes(media.uid))
      }
    },
    // 是否已全選媒體
    isSelectedAllMedia (id) {
      const mediaList = this.mediaList.find(media => media.categoryId === id)
      if (mediaList) {
        return this.isPrime
          ? mediaList.medias.every(media => this.media.includeIds.includes(media.uid))
          : mediaList.medias.every(media => this.media.excludeIds.includes(media.uid))
      }
    },
    // 判斷id是否已被選取
    hasMediaId (mediaId) {
      return this.isPrime
        ? this.media.includeIds.includes(mediaId)
        : this.media.excludeIds.includes(mediaId)
    },
    // 移除tag
    removeTag (mediaId) {
      const mediaIndex = this.isPrime
        ? this.media.includeIds.indexOf(mediaId)
        : this.media.excludeIds.indexOf(mediaId)
      if (mediaIndex !== -1) {
        this.isPrime
          ? this.media.includeIds.splice(mediaIndex, 1)
          : this.media.excludeIds.splice(mediaIndex, 1)
      }
    },
    // 移除該媒體分類底下所有tag
    removeAllTag (id) {
      this.mediaList
        .find(medias => medias.categoryId === id)
        .medias.forEach(media => {
          const mediaIndex = this.media.includeIds.indexOf(media.uid)
          if (mediaIndex !== -1) {
            this.media.includeIds.splice(mediaIndex, 1)
          }
        })
    },
    // 選取媒體分類與底下全部的媒體
    selectAllHoverMedia (isChecked) {
      this.hoverMediaList.forEach(media => {
        if (isChecked) {
          this.media.includeIds.push(media.uid)
        } else {
          this.media.includeIds.splice(this.media.includeIds.indexOf(media.uid), 1)
        }
      })
      this.media.includeIds = [...new Set(this.media.includeIds)]
    },
    // 點擊pop-menu範圍外關閉視窗
    outsideClick (e) {
      const target = e.target
      const el = this.$el.querySelector('.popup-menu')
      if (el !== target && !el.contains(target) && this.showPopupMenu) {
        this.showPopupMenu = !this.showPopupMenu
      }
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'media', value: Boolean(this.warningText) })
      this.updateSelectedData([
        {
          key: 'media',
          value: this.media
        },
        {
          key: 'condition.media',
          value: {
            is_prime: this.media.isPrime,
            media_ids: this.media.isPrime
              ? this.media.includeIds
              : this.media.excludeIds
          }
        }
      ])
    },
    // 檢查
    checkExcludeIds (uid) {
      return !this.media.excludeIds.includes(uid) && this.media.excludeIds.length >= this.maxExcludeMedia
    }
  }
}
</script>

