<!-- 條件組合工具列(我的最愛) -->
<template>
  <div class="favorite">
    <div class="favorite-tool fixed" :class="{ 'show': showToolBar}">
      <el-button
        class="btn-reset"
        @click="resetConfirm">
        恢復預設全選
      </el-button>
      <el-button
        class="btn-load"
        @click="openFavoriteList = true"
        type="primary">
        載入儲存的條件組合
      </el-button>
      <el-input class="favorite-name" v-model="favoriteName"></el-input>
      <el-button
        class="btn-save"
        @click="checkFavoriteName"
        type="primary">
        儲存
      </el-button>
    </div>
    <!-- 條件組合畫面 -->
    <el-dialog
      :visible.sync="openFavoriteList"
      :show-close="false"
      top="10vh"
      width="80%"
      center>
      <!-- Filter -->
      <div class="filter">
        <el-input
          v-model="searchKeyword"
          placeholder="請輸入條件組合名稱"
          clearable>
        </el-input>
      </div>
      <el-table
        v-loading="dataLoading"
        :data="tableData"
        :header-row-class-name="`data-table__head`"
        height="350"
        style="width: 100"
        class="data-table favorite-table">
        <template v-for="{label, prop, width, align} in columnConfig">
          <!-- props -->
          <el-table-column
            v-if="prop !== 'actions'"
            :key="prop"
            :label="label"
            :prop="prop"
            :align="align"
            :width="width"
            :formatter="columnDataFormat"
            sortable>
          </el-table-column>
          <!-- functions -->
          <el-table-column
            v-if="prop === 'actions'"
            :key="prop"
            :label="label"
            :align="align"
            :width="width">
            <template slot-scope="scope">
              <el-button
                @click="getFavorite(scope.row.id)"
                type="text">
                載入
              </el-button>
              <el-button
                @click="deleteFavorite(scope.row.id)"
                type="text">
                刪除
              </el-button>
            </template>
          </el-table-column>
        </template>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="openFavoriteList = false">關閉</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import dateFormat from 'date-fns/format'
import favoriteApi from '@/api/favorites'
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
  name: 'FavoriteTool',
  props: {
    scrollTop: {
      type: Number
    },
    favoriteData: {
      type: Object
    }
  },
  data () {
    return {
      showDeleteConfirm: false, // 顯示刪除確認視窗
      showSameNameConfirm: false, // 顯示同名確認視窗
      openFavoriteList: false, // 展開列表
      searchKeyword: '', // 列表搜尋關鍵字
      favoriteName: '條件組合', // 預設組合名稱
      showToolBar: true,
      currentScrollTop: 0,
      //
      favoriteList: [],
      columnConfig: [
        { label: '條件組合名稱', prop: 'name', width: null, align: 'left' },
        { label: '客戶名稱', prop: 'client_name', width: null, align: 'left' },
        { label: '廣告活動名稱', prop: 'campaign_name', width: null, align: 'left' },
        { label: '建立日期', prop: 'updated_at', width: '120px', align: 'center' },
        { label: '功能', prop: 'actions', width: '120px', align: 'center' }
      ]
    }
  },
  computed: {
    ...mapState('root', ['dataLoading']),
    ...mapState('calculation', ['dataSource', 'selectedData']),
    ...mapGetters('root', ['userId']),
    // 列表資料，如果有關鍵字就做過濾
    tableData () {
      return this.searchKeyword
        ? this.favoriteList.filter(favorite => favorite.name.includes(this.searchKeyword))
        : this.favoriteList
    }
  },
  watch: {
    scrollTop () {
      this.scrollEvent()
    },
    // 打開時取得列表資料
    openFavoriteList () {
      if (this.openFavoriteList) {
        this.getFavoriteListData()
      }
    }
  },
  created () {
    this.getFavoriteListData()
  },
  methods: {
    ...mapActions('calculation', ['setDataSource', 'resetSelectedData']),
    // 清空確認視窗
    resetConfirm () {
      this.$confirm('確定要清空目前的計算選項，回到全選狀態?', {
        cancelButtonText: '取消',
        confirmButtonText: '確定'
      }).then(() => {
        this.resetSelectedData()
      }).catch(() => {})
    },
    // 取得條件組合列表資料
    getFavoriteListData () {
      favoriteApi.getFavoriteListData(this.userId).then(res => {
        this.favoriteList = res.data.data.sort((a, b) => b.id - a.id)
      })
    },
    // 儲存計算檢查
    checkFavoriteName () {
      if (!this.selectedData.baseInfo.campaignName) {
        return this.$alert('請先輸入廣告活動名稱')
      }
      if (!this.selectedData.client_name) {
        return this.$alert('請先輸入客戶名稱')
      }
      if (!this.favoriteName) {
        return this.$alert('請輸入條件組合名稱')
      }
      // 判斷是否已有相同名稱的組合
      const checkSameName = this.favoriteList.find(data => data.name === this.favoriteName)
      if (checkSameName) {
        this.$confirm('此名稱已有條件組合使用，確定要覆寫?', {
          cancelButtonText: '取消',
          confirmButtonText: '確定'
        }).then(() => {
          this.createFavorite()
        }).catch(() => {})
      } else {
        this.createFavorite()
      }
    },
    // 建立條件組合
    createFavorite () {
      const favoriteData = {
        name: this.favoriteName,
        campaign_name: this.selectedData.baseInfo.campaignName,
        client_id: this.selectedData.baseInfo.clientId,
        client_name: this.selectedData.baseInfo.clientName,
        agency_id: this.selectedData.baseInfo.agencyId,
        agency_name: this.selectedData.baseInfo.agencyName,
        condition: this.selectedData,
        current_user_id: this.userId
      }
      favoriteApi.createFavorite(favoriteData).then(res => {
        this.showSameNameConfirm = false
        this.getFavoriteListData()
        this.$alert('條件組合已儲存!')
      })
    },
    // 取得單筆條件組合資料，並寫到選項中
    getFavorite (id) {
      favoriteApi.getFavorite(id).then(res => {
        this.$alert('條件組合已載入!')
        this.favoriteName = res.data.data.name
        this.setDataSource(res.data.data.condition)
        this.openFavoriteList = false
      })
    },
    // 刪除條件組合
    deleteFavorite (id) {
      this.$confirm('確定要刪除此條件組合?', {
        cancelButtonText: '取消',
        confirmButtonText: '確定'
      }).then(() => {
        favoriteApi.deleteFavorite(id).then(res => {
          this.getFavoriteListData()
        })
      }).catch(() => {})
    },
    // 列表formatter
    columnDataFormat (row, column) {
      if (!this.$lodash.isNil(row[column.property])) {
        switch (column.property) {
          case 'updated_at':
            return dateFormat(row[column.property], 'YYYY-MM-DD')
          default:
            return row[column.property]
        }
      }
    },
    scrollEvent () {
      this.showToolBar = (this.scrollTop < this.currentScrollTop) || this.scrollTop === 0
      this.currentScrollTop = this.scrollTop
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/styles/z-index.scss';
.favorite {
  &-name {
    margin: 0 8px 0 24px;
  }
  &-tool {
    position: absolute;
    display: flex;
    margin-top: -80px;
    padding: 8px 16px;
    width: 100%;
    background-color: #dfefff;
    &.fixed {
      position: fixed;
      z-index: map-get($z-index, CalculationTool);
      margin-top: -138px;
      min-width: 592px;
      width: calc(100% - 684px);
      transition: 0.5s;
      &.show {
        margin-top: -80px;
      }
    }
  }
  .filter {
    .el-input {
      width: 262px;
    }
  }
  .el-dialog__body {
    padding: 16px;
  }
  .el-dialog__header {
    display: none;
  }
  .el-dialog__footer {
    padding: 16px;
  }
  .favorite-table {
    margin: 16px 0 -16px 0;
