<!-- 廣告走期 -->
<template>
  <div class="play-date">
    <div class="option">
      <span class="option-title required">廣告走期</span>
      <span
        class="option-warning"
        v-if="warningText">
        {{ warningText }}
      </span>
      <div class="option-content">
        <el-date-picker
          v-model="playDate"
          type="daterange"
          range-separator="~"
          start-placeholder="開始日期"
          end-placeholder="結束日期"
          value-format="yyyy-MM-dd"
          class="option-item date-picker"
          :picker-options="datePickerOptions"
          :clearable="false">
        </el-date-picker>
      </div>
    </div>
  </div>
</template>

<script>
import eachDay from 'date-fns/each_day'
import getDate from 'date-fns/get_date'
import getTime from 'date-fns/get_time'
import addMonths from 'date-fns/add_months'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'PlayDate',
  data () {
    return {
      eachDay,
      getDate,
      getTime,
      addMonths,
      playDate: [],
      datePickerOptions: {}
    }
  },
  computed: {
    ...mapState('calculation', ['dataSource']),
    warningText () {
      return this.$lodash.isEmpty(this.playDate) ? '請選擇走期' : null
    },
    days () {
      const eachDay = this.eachDay(new Date(this.playDate[0]), new Date(this.playDate[1]))
      return [...new Set(eachDay.map(day => this.getDate(day)))]
    }
  },
  watch: {
    playDate () {
      this.updateData()
    },
    dataSource: {
      handler () {
        // 載入計算結果時將走期清空
        this.playDate = []
      },
      deep: true,
      immediate: true
    }
  },
  created () {
    this.setDatePickerOptions()
  },
  methods: {
    ...mapActions('calculation', ['updateSelectedData', 'updateRequireCheckData']),
    // 設定日期選擇限制
    setDatePickerOptions () {
      const dateRange = {
        start: this.getTime(new Date()),
        end: this.addMonths(new Date(), 1)
      }
      this.datePickerOptions = {
        disabledDate (date) {
          return date.getTime() >= dateRange.end || date.getTime() <= dateRange.start
        }
      }
    },
    // 更新選取資料
    updateData () {
      this.updateRequireCheckData({ key: 'playDate', value: Boolean(this.warningText) })
      this.updateSelectedData([
        { key: 'playDate', value: this.playDate },
        { key: 'start_date', value: this.playDate[0] },
        { key: 'end_date', value: this.playDate[1] },
        { key: 'condition.days', value: this.days }
      ])
    }
  }
}
</script>
